import HomeScreen from "./components/HomeScreen.vue";
import HelpScreen from "./components/HelpScreen.vue";
import ThankyouScreen from "./components/ThankyouScreen.vue";
import ProductListScreen from "./components/ProductListScreen.vue";
import ProductDetailScreen from "./components/ProductDetailScreen.vue";
import PouringScreen from "./components/PouringScreen.vue";
import {
  mapActions
} from "vuex";
import BluetoothTerminal from './assets/js/BluetoothTerminal';

export default {
  name: "app",
  components: {
    HomeScreen,
    HelpScreen,
    ThankyouScreen,
    ProductListScreen,
    ProductDetailScreen,
    PouringScreen
  },
  data: function () {
    return {
      terminal: null,
      deviceId: "",
      rfIdNumber: "",
      taps: [],
      deviceDetails: {},
      userDetails: {},
      productDetails: {},
      currentPage: 1,
      lastPage: 1,
      currentUserBalance: 0
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    ...mapActions(["getModule", "getUserDetails", "saveConsumerHistory"]),
    init() {
      // Obtain configured instance.
      this.terminal = new BluetoothTerminal('6e400001-b5a3-f393-e0a9-e50e24dcca9e', '6e400002-b5a3-f393-e0a9-e50e24dcca9e');
      this.connectToDevice();
      var self = this;
      this.terminal.receive = function (command, data) {
        self.receiveData(command, data);
      };
      this.terminal._log = function (...messages) {
        // We can't use `super._log()` here.
        messages.forEach((message) => {
          console.log(message); // eslint-disable-line no-console
        });
      };
    },
    changePage(lastPage, page) {
      this.lastPage = lastPage;
      this.currentPage = page;
    },
    goToBack() {
      this.currentPage = this.lastPage;
    },
    goToProductDetails(product, tapNumber) {      
      this.lastPage = 4;  
      this.currentPage = 5; 
      this.productDetails = product;
      this.productDetails.TapNumber = tapNumber;
    },
    serve() {
      this.lastPage = 5;  
      this.currentPage = 6; 
      this.terminal._startPour();
    },
    callFunction() {
      this.deviceId = '0CFF8CE350CC';
      this.checkDevice();
    },
    receiveData(command, data) {
      if (command == 21) {
        this.deviceId = '0CFF8CE350CC';
        this.checkDevice();       
      } else if (command == 31) {
        alert("RFID receive"+ data);
        this.rfIdNumber = data.toUpperCase();
        this.checkRFIDExists();       
      } else if (command == 41) {
        //Tap Number 1-Left 2-Right
        var tapNumber = this.productDetails.TapNumber;
        this.terminal._sendToDevice(command, tapNumber);
      } else if (command == 42) {
        //Volume of Tap in ml
        var volume = this.productDetails.Volume;
        volume = volume * 1000;
        this.terminal._sendToDevice(command, volume);
      } else if (command == 43) {
        //Beer price in cent
        var price = this.productDetails.Product.Price;
        price = price * 100;
        this.terminal._sendToDevice(command, price);
      } else if (command == 44) {
        //User Amount in cent
        var userBalance = this.userDetails.CardCustomer[0].Amount;
        userBalance = userBalance * 100;
        this.terminal._sendToDevice(command, userBalance);
      } else if (command == 32 || command == 33 || command == 34 || command == 35 || command == 51) {
        var lastBalance = (data/100);        
        this.saveHistory(lastBalance);
      } else if (command == 36) {
        //continues data receive
        this.currentUserBalance = (data/100);
      } 
    },
    connectToDevice() {
      this.terminal.connect().
      then(() => {
        alert(this.terminal.getDeviceName() ? this.terminal.getDeviceName() : "Default Device");
      });
    },
    checkRFIDExists() {
      var isValid = false;
      var self = this;
      this.getUserDetails(this.rfIdNumber).then(response => {
        if (response) {
          try{
            self.userDetails = response;
            self.currentUserBalance = self.userDetails.CardCustomer[0].Amount;          
            self.currentPage = 4;
          }
          catch(ex){
            alert(ex);
          }
           
          }
          else{
            self.terminal._sendToDevice(31, 0);
          }
        },
        error => {});
      return isValid;
    },
    checkDevice() {
      var isValid = false;
      var self = this;
      try {
        this.getModule(this.deviceId).then(response => {
            if (response) {             
              self.deviceDetails = response;
              self.taps =  self.deviceDetails.Taps;
              isValid = true;
              self.terminal._sendToDevice(21, 1);
            }
            else
            {
              self.terminal._sendToDevice(21, 0);
            }
          },
          error => {
            self.terminal._sendToDevice(21, 0);
          });
      } catch (ex) {

      }    
      return isValid;
    },
    sleep(milliseconds){
      return new Promise(resolve => setTimeout(resolve, milliseconds))
    },
    saveHistory(data) {
      this.currentPage = 3;
      this.sleep(5000).then(() => {    
        this.currentPage = 1;      
       });       
      var price = this.productDetails.Product.Price;
      var quantity = 0;
      var originalPrice = this.userDetails.CardCustomer[0].Amount;
      quantity = ((originalPrice - data) * 1000)/price;
      var model = {
        User: JSON.stringify(this.userDetails.CardCustomer[0].User),
        Id_Card: this.userDetails.Id_Card,
        Type: "Consumption",
        Amount: data,
        Quantity: quantity,
        Product: JSON.stringify(this.productDetails.Product),
        Id_Tap: this.productDetails.Id_Tap,
        Id_Location: this.deviceDetails.Id_Location,
        Id_Module: this.deviceDetails.Id_Module,
        Id_Totem: this.deviceDetails.Id_Totem
      };
      this.saveConsumerHistory(model).then(response => {
          if (!response.error) {
            this.clearData();
          }
        },
        error => {

        });
    },
    clearData() {
      this.userDetails = {};
      this.rfIdNumber = 0;
      this.productDetails = {};
    },
    startPouring() {
      this.terminal._startPour();
    },
    tapStop() {
      this.terminal._tapClosed();
    }
  }
};